<template >
    <div class="listing mt-2">
        <div class="table-responsive">
            <table class="table table-bordered mt-0" id="itmgrplisting">
                <thead class="thead-bg">
                    <tr>
                        <th class="text-center"><span href="javascript:;" id="TAG">#</span></th>
                        <th class="text-center"><span href="javascript:;" id="TAG">{{ $t('ProductName') }}</span></th>
                        <th class="text-center"><span href="javascript:;">{{ $t('TermsAndConditions') }}</span></th>
                        <th class="text-center"><span href="javascript:;">{{ $t('AvailableQuantity') }}</span></th>
                        <th class="text-center"><span id="group_name">{{ $t('PendingCheckOutRequest') }}</span></th>
                        <th class="text-center"><span id="ReadTime">{{ $t('RequestQuantity') }} </span></th>
                        <th class="text-center"><span id="AttachedItems">{{ $t('Compartment') }}</span></th>
                        <th class="text-center"><span id="AttachedItems">{{ $t('Action') }} </span></th>
                    </tr>
                </thead>

                <tbody v-if="listData.length > 0">
                    <tr class=" showassetcataloguegroup " v-for="item of listData" :key="item">
                        <td class="textaligncenter align-middle item-data text-center">
                            <span class="showcatalog hidden showassetcatalog"></span>

                        </td>

                        <td class="align-middle ad item-name text-center">{{ `${item.AssetCatalogName}
                                                    (${item.AssetUniqueName})` }}</td>

                        <td v-if="item.IsEnableTerms == 0" class="align-middle terms-condition" style="white-space:normal;">
                            <span>N/A</span>
                        </td>
                        <td class="align-middle terms-condition text-center" v-else>
                            <div class="custom-control custom-checkbox">
                                <input class="chkItems custom-control-input" type="checkbox" name="checkbox"
                                    :checked="item.IsEnableTerms == 1">
                                <label class="custom-control-label universal-custom-control-label pt-1"></label>
                            </div>

                        </td>

                        <td class="textalignright align-middle text-center">
                            {{ item.AvailableItems }}
                        </td>

                        <td class="textalignright align-middle text-center">
                            {{ item.PendingAvailableQuantity }}
                        </td>

                        <td class="align-middle text-center">


                            <Field name="Requested Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                <input @keyup="checkQuantity(item)" v-model="item.PendingTotalAvailableItems" type="number"
                                    min="1" maxlength="8"
                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />

                            </Field>



                        </td>


                        <td class="align-middle text-center">
                            <span>N/A</span>
                        </td>

                        <td class="text-center align-middle single-action action-group">

                            <!-- v-if="item.HasRelatedProducts" -->

                            <a v-if="item.HasRelatedProducts" href="javascript:;" class="relatedproduct"
                                v-on:click="relatedProductDetails(item)" title="Related items">
                                <i class="fa fa-sitemap text-primary" aria-hidden="true"></i>
                            </a>

                            <a href="javascript:;" class="recyclebin  enable" v-on:click="deleteItem(item)"
                                title="Delete"><i class="fa fa-trash text-danger action-icon"></i></a>

                        </td>
                    </tr>

                </tbody>
                <tbody v-else>
                    <tr v-if="NoRecordfound">
                        <td colspan="8" class="text-center text-danger no-record">{{ $t('NoRecordfound') }}</td>
                    </tr>
                </tbody>
            </table>

            <RelatedProduct v-if="isVisible == true" :GetRelatedProduct="GetRelatedProduct"></RelatedProduct>

        </div>
    </div>
</template>



<script>
import RelatedProduct from "../Modal/RelatedProductPopup.vue";
import DataService from '../../service/DataService';

export default {

    components: {
        RelatedProduct
    },
    props: {
        listData: {
            type: Array
        }
    },

    data() {

        return {
            NoRecordfound: true,
            isVisible: false,
            GetRelatedProduct: []
        }
    },

    methods: {

        checkQuantity: function (item) {
            var vm = this;
            let TotalQuantity = item.AvailableItems;
            let currentValue = item.PendingTotalAvailableItems;
            if (currentValue > TotalQuantity) {
                vm.listData.forEach(ele => {

                    if (ele.AvailableItems == ele.PendingAvailableQuantity) {
                        ele.PendingTotalAvailableItems = '';
                        return vm.ShowAlert(
                            vm.$t("Requested quantity not available. "),
                            "warning",
                            vm.$t("Alert")
                        );
                    }
                    if (ele.AssetCatalogueId == item.AssetCatalogueId) {

                        vm.ShowAlert(
                            vm.$t(`Quantity can not be more than ${TotalQuantity}`),
                            "warning",
                            vm.$t("Alert")
                        );

                        return ele.PendingTotalAvailableItems = ''
                    }
                })

            }
            else {
                return;
            }
        },


        relatedProductDetails(item) {

            var vm = this;
            var assetCatalogId = item.AssetCatalogueId;

            var url = `id=${assetCatalogId}&type=ProductName`;
            DataService.GetRelatedAssets(url).then(response => {
                vm.GetRelatedProduct = response.data
                vm.isVisible = true;
            })

        },



        deleteItem(item) {

            var vm = this;
            vm.confirmR(vm.$t('Please verify that you want to delete this record'), true, false, function (result) {

                const index = vm.listData.findIndex(x => x.AssetCatalogueId === item.AssetCatalogueId);

                if (index !== -1) {
                    vm.listData.splice(index, 1);
                }

                return vm.ShowAlert(
                    vm.$t("Item has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")
                );

            });
        },

        CloseRelatedProductsPopup(){
            var vm=this;
            vm.isVisible=false;
        }

    }

}

</script>
<style></style>